export class CartModel {
    constructor(events) {
        this._events = events;
        this._products = [];
        this._total = 0;
    }
    get events() {
        return this._events;
    }
    get products() {
        return this._products;
    }
    get total() {
        return this._total;
    }
    clear() {
        this._products = [];
        this._total = 0;
    }
    add(card) {
        this._products.push(card);
        this._total += card.price;
    }
    remove(idToRemove) {
        this._total -= this.products.find((card) => card.id === idToRemove).price;
        this._products = this._products.filter((card) => card.id !== idToRemove);
    }
}
